/* Inicio - Estrutura do menu */
.nav {
    margin: 0px;
    padding: 0px;

    list-style: none;
    background-color: black;
}
.nav li { display: inline; }
/* Fim - Estrutura do menu */

/* Inicio - Botões do menu principal */
.nav li a {
    display: inline-block;
    padding: 5px 10px;

    text-decoration: none;
    font-size: 19px;    
}

.nav li > a {
    background-color: rgb(0, 0, 50);

    color: rgb(255, 255, 255);

    border-width: 1px;
    border-style: groove;
    border-color: #666; 
}

.nav li a:hover {
    background-color: rgb(10, 10, 200);
    color: #fff;
}
/* Fim - Botões do menu principal */

/* Inicio - Menu secundario (Dropdown) */
.nav .nav-dropdown {
    display: none;
    position: absolute;
    z-index: 1;    
}
.nav li:hover .nav-dropdown { display: block; }
/* Fim - Menu secundario (Dropdown) */

/* Inicio - Opções do menu secundario (Dropdown) */
.nav .nav-dropdown a {
    display: block;

    background-color: #fff;

    color: #000;

    border-color: #666;
    border-width: 1px;
    border-style: solid;

    border-top: none;
}
.nav .nav-dropdown a:hover {
    background-color: rgb(0, 0, 50);
}
/* Fim - Opções do menu secundario (Dropdown) */

